export default {
  data () {
    return {
      form: {
        name: '邓海标',
        mobile: 13680065830,
        address: '陈家北二巷',
        area: '528300'
      },
      area:'',
      cascadeData :[
        {
          value: 'Fruit',
          text: 'Fruit',
          children: [
            {
              value: 'Apple',
              text: 'Apple',
              children: [{ value: 1, text: 'One' }, { value: 2, text: 'Two' }]
            },
            {
              value: 'Orange',
              text: 'Orange',
              children: [{ value: 3, text: 'Three'}, { value: 4, text: 'Four' }]
            }
          ]
        },
        {
          value: 'Drink',
          text: 'Drink',
          children: [
            {
              value: 'Coffee',
              text: 'Coffee',
              children: [{ value: 1, text: 'One' }, { value: 2, text: 'Two' }]
            },
            {
              value: 'Tea',
              text: 'Tea',
              children: [{ value: 1, text: 'One' }, { value: 3, text: 'Three'}]
            }
          ]
        }
      ]
    }
  },
  mounted () {
    this.cascadePicker = this.$createCascadePicker({
      title: 'Cascade Picker',
      data: this.cascadeData,
      selectedIndex: [0, 1, 0],
      onSelect: this.selectHandle,
      onCancel: this.cancelHandle
    })
  },
  methods: {
    showCascadePicker() {
      this.cascadePicker.show()
    },
    selectHandle(selectedVal, selectedIndex, selectedText) {
      this.form.area=`${selectedText.join(',')}`
    },
    cancelHandle() {

    }
  }

}



